
/* User Info */
.user-info-in-nav-wrap{
    display:table;
    & > div, & > ul{
        display:inline-flex;
        vertical-align: middle;
    }
    .user-info-avatar{
        width: 63px;
        height: 63px;
        margin-right: 15px;
    }
    .user-info-item-group{
        flex-direction: column;
    }
}

.user-info-avatar .avatar {
    width: 35px;
    height: 35px;
    border-radius: 50%;
}


/**
* Elementor / jvbpd-nav-menu widget
*
*/
.jvbpd-nav-menu-widget{
    li.nav-item{
        position:relative;
        & ul.wide-nav-overlay{
            position:absolute;
        }
    }
    & > .mobile-opener{ display:none; }
    & > div.menu-wrap.show{ display:block; }
    @media( max-width:767px ) {
        &.device-mobile{
        & > .mobile-opener{ display:block; }
        & > div.menu-wrap{
            display:none;
            &.show{ display:block; }
            & ul.nav{
                width:200px;
                display: block;
                &.mm-collapse:not(.mm-show){display:none;}
                li{float:none;word-break:break-wrod;}
            }
        }
            ul.wide-nav-overlay{display:none;}
            .jvbpd-nav-menu > li:hover > ul{position:relative;}
            .jvbpd-nav-menu .menu-item-depth-0 a .fa{float:none;}
        }
    }
    @media( max-width:1023px ) {
        &.device-tablet{
        & > .mobile-opener{ display:block; }
        & > div.menu-wrap{
            display:none;
            &.show{ display:block; }
            & ul.nav{
                display: block;
                &.mm-collapse:not(.mm-show){display:none;}
                li{float:none;}
            }
        }
            ul.wide-nav-overlay{display:none;}
            .jvbpd-nav-menu > li:hover > ul{position:relative;}
        }
    }
    span.menu-titles{
        transition:color .3s ease-in-out;
        -webkit-transition:color .3s ease-in-out;
        -moz-transition:color .3s ease-in-outs;
        -o-transition:color .3s ease-in-out;
    }
}

.elementor-widget-jvbpd_nav_menu{
    .jvbpd-nav-menu-widget{
        @media( max-width:767px ) {
            &.device-mobile{
                & div.menu-wrap{
                    position:absolute;
                }
            }
        }
        @media( max-width:1023px ) {
            &.device-tablet{
                & div.menu-wrap{
                    position:absolute;
                }
            }
        }
    }
    &.jvbpd-nav-menu-full-width{
        .jvbpd-nav-menu-widget{
            @media( max-width:767px ) {
                &.device-mobile{
                    & div.menu-wrap{
                        left:0;
                        right:0;
                        margin:-10px;
                    }
                }
            }
            @media( max-width:1023px ) {
                &.device-tablet{
                    & div.menu-wrap{
                        left:0;
                        right:0;
                        margin:-10px;
                    }
                }
            }
        }
    }

    &.menu-hover-effect-left-right{
        .nav-item.menu-item-depth-0 > a.nav-link > span{
            position:relative;
            display:block;
            &:after{
                content:'';
                position:absolute;
                bottom:0;
                left:0;
                width:0%;
                border-bottom:solid 2px #aaaaaa;
                transition:.4s;
                -webkit-transition:.4s;
                -o-transition:.4s;
            }
            &:hover{
                &:after{ width:100%; }
            }
        }
    }
    &.menu-hover-effect-center{
        .nav-item.menu-item-depth-0 > a.nav-link > span{
            position:relative;
            display:block;
            &:after{
                content:'';
                position:absolute;
                bottom:0;
                left:50%;
                width:0%;
                border-bottom:solid 2px #aaaaaa;
                transition:.4s;
                -webkit-transition:.4s;
                -o-transition:.4s;
            }
            &:hover{
                &:after{ left:0%; width:100%; }
            }
        }
    }

}

/**
 *	Menu opener
 *
 */
 .jvbpd-canvas-container {
    padding: 0;
    position: absolute;
    width: 100%;
    height: auto;
    top: 0;
    z-index: 100;
    visibility: hidden;
    background-color: #ffffff;
    transition: all 0.5s;
    &.ani-ltr {
      transform: translate3d(-99%, 0, 0);
      -webkit-transform: translate3d(-99%, 0, 0);
      -moz-transform: translate3d(-99%, 0, 0);
      -ms-transform: translate3d(-99%, 0, 0);
      -o-transform: translate3d(-99%, 0, 0);
      left: -1%;
    }
    &.ani-rtl {
      transform: translate3d(99%, 0, 0);
      -webkit-transform: translate3d(99%, 0, 0);
      -moz-transform: translate3d(99%, 0, 0);
      -ms-transform: translate3d(99%, 0, 0);
      -o-transform: translate3d(99%, 0, 0);
      right: -1%;
    }
    &.ani-center {
      transform: scale(0.5);
      -webkit-transform: scale(0.5);
      -moz-transform: scale(0.5);
      -ms-transform: scale(0.5);
      -o-transform: scale(0.5);
      opacity: 0;
      top: 0;
      left: 0;
    }
    &.jvbpd-visible {
      transform: translate3d(0, 0, 0);
      -webkit-transform: translate3d(0, 0, 0);
      -moz-transform: translate3d(0, 0, 0);
      -ms-transform: translate3d(0, 0, 0);
      -o-transform: translate3d(0, 0, 0);
      &.ani-ltr {
        left: 0;
        z-index:5000;
        -webkit-z-index:5000;
        -moz-z-index:5000;
        -ms-z-index:5000;
        -o-z-index:5000;
      }
      &.ani-rtl {
        right: 0;
      }
      &.ani-center {
        transform: scale(1);
        -webkit-transform: scale(1);
        -moz-transform: scale(1);
        -ms-transform: scale(1);
        -o-transform: scale(1);
        opacity: 1;
      }
      visibility: visible;
      background-color: #000000;
      &:after {
        content: "";
        position: absolute;
        display: block;
        top: 50%;
        left: 50%;
        width: 75px;
        height: 50px;
        transform: translate(-50%, -50%);
        background-image: url("data:image/svg+xml;base64,");
      }
      &.loaded {
        &:after {
          display: none;
        }
      }
    }
    .jvbpd-nav-menu.flex-column > li {
        display: flex;
        flex-direction:column;
    }
    .metismenu li.nav-item:hover > ul,
    .metismenu li.nav-item:hover > ul.collapse {
        display: flex !important;
        height:auto !important;
        flex-direction: column;
    }
        @media (max-width: 767px){
        .jvbpd-nav-menu .menu-item-depth-0 a .fa {
            float: right;
            position: relative;
            transform: translateY(50%);
        }
    }
    .jvbpd-nav-menu .sub-menu-second li a{
        display:inline-block;
    }
    .jvbpd-nav-menu > li > ul li:hover > ul {
        position:unset;
    }
  }
  /*Canvas menu on - display*/
  body.jvbpd-menu-active{
      #wrapper{
          display:none;
          -webkit-display:none;
          -moz-display:none;
          -ms-display:none;
          -o-display:none;
        }
  }