	*/
.jv-preloader-inner > div {
  background: #34495e;
  border-color: #34495e !important;
}

.jv-preloader-scale {
  transform: scale(1);
}

.jv-preloader-inner {
  position: relative;
  display: inline-block;
}

.jv-preloader-holder {
  position: relative;
  display: block;
  top: 50%;
  width: 100%;
  padding: 0;
  margin: 0 auto;
  line-height: 0;
  text-align: center;
}

.jv-preloader {
  display: block;
}

.jv-holder:before, .jv-preloader {
  position: absolute;
  top: 0;
  height: 100%;
  left: 0;
  width: 100%;
}

/*------------------------------------------------------------------
	["Pacman animation"]
	*/
.pacman > div:first-of-type,
.pacman > div:nth-child(2) {
  width: 0;
  height: 0;
  border-right: 25px solid transparent !important;
  border-top: 25px solid;
  border-left: 25px solid;
  border-bottom: 25px solid;
  border-radius: 25px;
  position: relative;
  left: -30px;
  background-color: transparent !important;
}

@-webkit-keyframes rotate_pacman_half_up {
  0%, 100% {
    transform: rotate(270deg);
  }
  50% {
    transform: rotate(360deg);
  }
}
@keyframes rotate_pacman_half_up {
  0%, 100% {
    transform: rotate(270deg);
  }
  50% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes rotate_pacman_half_down {
  0%, 100% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(0);
  }
}
@keyframes rotate_pacman_half_down {
  0%, 100% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(0);
  }
}
@-webkit-keyframes pacman-balls {
  0% {
    opacity: 0;
    transform: translate3d(0, 0, 0);
  }
  75% {
    opacity: 0.7;
    transform: translate3d(-75px, 0, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(-100px, 0, 0);
  }
}
@keyframes pacman-balls {
  0% {
    opacity: 0;
    transform: translate3d(0, 0, 0);
  }
  75% {
    opacity: 0.7;
    transform: translate3d(-75px, 0, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(-100px, 0, 0);
  }
}
.pacman {
  position: relative;
}

.pacman > div:nth-child(3) {
  -webkit-animation: pacman-balls 1s -0.66s infinite linear;
  animation: pacman-balls 1s -0.66s infinite linear;
}

.pacman > div:nth-child(4) {
  -webkit-animation: pacman-balls 1s -0.33s infinite linear;
  animation: pacman-balls 1s -0.33s infinite linear;
}

.pacman > div:nth-child(5) {
  -webkit-animation: pacman-balls 1s -0.01s infinite linear;
  animation: pacman-balls 1s -0.01s infinite linear;
}

.pacman > div:first-of-type {
  -webkit-animation: rotate_pacman_half_up 0.45s 0s infinite;
  animation: rotate_pacman_half_up 0.45s 0s infinite;
}

.pacman > div:nth-child(2) {
  -webkit-animation: rotate_pacman_half_down 0.45s 0s infinite;
  animation: rotate_pacman_half_down 0.45s 0s infinite;
  margin-top: -50px;
}

.pacman > div:nth-child(3),
.pacman > div:nth-child(4),
.pacman > div:nth-child(5),
.pacman > div:nth-child(6) {
  position: absolute;
  display: block;
  border-radius: 100%;
  width: 10px;
  height: 10px;
  top: 50%;
  margin-top: -5px;
  left: 80px;
}

