.jvbpd-grid > li {
  display: block;
  float: left;
  padding: 7px;
  width: 33%;
  opacity: 0;
}

.jvbpd-grid li.shown,
.no-js .jvbpd-grid li,
.no-cssanimations .jvbpd-grid li {
  opacity: 1;
