  }
}
@-webkit-keyframes cluster-animate {
  0% {
    -webkit-box-shadow: 0px 0px 0 5px rgba(199, 199, 199, 0.5);
  }
  50% {
    -webkit-box-shadow: 0px 0px 0 0px rgba(199, 199, 199, 0.5);
